                            <span class="p-name text-white">{{ $t('HelpArticles') }}</span>
                            <router-link to="/Help/HelpArticles"><span class="p-actions float-right"><a title=""
                                        class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                        data-original-title="Back">
                                        <em class="fa fa-angle-double-left"></em><br />{{ $t("Back") }}</a>

                                </span> </router-link>


                        </div>
                    </div>
                </div>

            </div>

            <div class="left-menu-tab new_tab nosrcoll">
