                        role="tablist">
                        <li class="nav-item" v-for="item in ModulesList" :key="item"
                            @click="ArticleListPerModule(item.ModuleId)">
                            <a checkPrivilege="Yes" :class="{ 'active': SelectModuleId == item.ModuleId }"
                                class="nav-link left-menu" style="cursor:pointer;">
                                <span v-html="item.icon"></span>{{ item.ModuleName }}
