                                                        </div>
                                                        <div :id="'FunctionCollapse' + name.articleId"
                                                            :class="{ 'collapse multi-collapse': activeAccordion != name.articleId, 'show': activeAccordion == name.articleId }"
                                                            role="tabpanel"
                                                            :aria-labelledby="'MainFunction' + name.articleId"
                                                            data-parent="#accordionEx1">
                                                            <div class="card-body p-0 customScrollForLeftBar llt"
                                                                style="max-height:300px;">
                                                                <div class="form-group">
                                                                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix"
                                                                        style="max-height: 214px; overflow: auto; display: block;">
                                                                        <li v-html="name.articlecontent"></li>
                                                                    </ul>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>
                                            <div class="row" v-if="IsDataNotFoundShow">
                                                <div class="col-md-12">
                                                    <div class="border text-center text-danger no-record mt-4 p-2">
                                                        No Record Found
                                                    </div>
                                                </div>
                                            </div>


                                        </div>
                                        <div class="left-menu-tab new_tab nosrcoll float-right">

                                            <h6>Related Content</h6>
                                            <ul
                                                class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix rtfix">

                                                <li class="nav-item font-14"><a class="nav-link left-menu"
                                                        @click="DownloadUsermanual(ModuleId)"><em
                                                            class="fa fa-download text-info" aria-hidden="true"></em>
                                                        Download User Manual</a></li>

                                                <router-link to="/Help/VideoTutorials">
                                                    <li class="nav-item font-14"><a class="nav-link left-menu"><em
                                                                class="fa fa-youtube-play text-danger"
                                                                aria-hidden="true"></em>
                                                            Watch Video Tutorial</a></li>
                                                </router-link>
                                            </ul>


                                            <h6 class="mt-3">Still need help?</h6>
                                            <ul
                                                class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix rtfix">
                                                <li class="nav-item font-14 p-2">
                                                    The Talygen Support Team is here for you.<br />
                                                    <button href="javascript;;"
                                                        class="btn btn-primary text-center text-white justify-center mt-2"
                                                        @click="OpenChat">Chat
                                                        with us</button>
                                                </li>
                                            </ul>


                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <div v-if="IsLiveChatShowArticlePage">
                <noscript>
                    <a href="https://www.livechatinc.com/chat-with/13332330/" rel="nofollow">Chat with us</a>,
                    powered by <a href="https://www.livechatinc.com/?welcome" rel="noopener nofollow"
                        target="_blank">LiveChat</a>
                </noscript>
            </div>


        </section>
    </div>
</template>

<script>
import DataService from '../../services/DataService';
import VueEasyLightbox from 'vue-easy-lightbox';
import Search from "../../../src/views/HelpAndGuide/Search.vue";

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());


export default {
    name: 'HelpArticlesPerModule',

    props: {
        IsLiveChatShowHelpGuidePage: Boolean
    },
    components: {
        VueEasyLightbox, Search
    },
    data() {

        return {
            mImage: null,
            Modal1: null,
            Image1: null,
            Image2: null,
            captionText: null,
            ModulesList: [],
            HelpArticlePerModuleList: [],
            IsDataNotFountShow: false,
            visible: false,
            imgs: [],
            indexRef: 0,
            PlaceHolderText: 'Search by Keyword',
            IsLiveChatShowArticlePage: false,
            ModuleId: 0,
            isLoading: false,
            Path: Boolean,
            Id: null,
            Module_Id: null,
            SelectModuleId: '',
            activeAccordion: null,
            DefaultModuleId: 0,
            ModuleName: '',
            ArticlesContent: '',
            IsDataNotFoundShow: false,
            IsDataShow: true,

        }
    },
    created: async function () {

        this.Id = this.$route.params.Id;
        this.Module_Id = this.$route.params.ModuleId;
        await this.ModuleList();
        if (this.Module_Id > 0) {
            this.ArticleListPerModule(this.Module_Id)
        }
        else {
            this.ArticleListPerModule(this.DefaultModuleId)
        }

        this.activeAccordion = this.Id;

    },
    methods: {
        OpenChat: function () {
            var vm = this;
            // vm.ShowAlert("Chat Already Opened", "success", true, vm.$t("Alert"));           

            vm.IsLiveChatShowArticlePage = true;
            setInterval(function () {
                window.__lc = window.__lc || {};
                window.__lc.license = 13332330;;
                (function (n, t, c) {
                    function i(n) {
                        return e._h ? e._h.apply(null, n) : e._q.push(n)
                    }
                    var e = {
                        _q: [],
                        _h: null,
                        _v: "2.0",
                        on: function () {
                            i(["on", c.call(arguments)])
                        },
                        once: function () {
                            i(["once", c.call(arguments)])
                        },
                        off: function () {
                            i(["off", c.call(arguments)])
                        },
                        get: function () {
                            if (!e._h) throw new Error("[LiveChatWidget] You can't use getters before load.");
                            return i(["get", c.call(arguments)])
                        },
                        call: function () {
                            i(["call", c.call(arguments)])
                        },
                        init: function () {
                            var n = t.createElement("script");
                            n.async = !0, n.type = "text/javascript", n.src = "https://cdn.livechatinc.com/tracking.js", t.head.appendChild(n)
                        }
                    };
                    !n.__lc.asyncInit && e.init(), n.LiveChatWidget = n.LiveChatWidget || e
                }(window, document, [].slice))
                setTimeout(() => {
                    LiveChatWidget.call("CompanyId", vm.CompanyName);
                    LiveChatWidget.call("UserID", vm.UserID);
                    LiveChatWidget.call("UserName", vm.UserName);
                    LiveChatWidget.call("EmailId", vm.EmailId);
                    LiveChatWidget.call("set_customer_name", vm.UserName);

                    LiveChatWidget.call("set_session_variables", {
                        UserName: vm.UserName,
                        CompanyName: vm.CompanyName,
                        UserID: vm.UserID,
                        EmailId: vm.EmailId
                    });

                    function onReady(initialData) {
                        var state = initialData.state;
                        var customerData = initialData.customerData.sessionVariables;
                        LiveChatWidget.call("set_session_variables", {
                            UserName: vm.UserName,
                            CompanyName: vm.CompanyName,
                            UserID: vm.UserID,
                            Email: vm.EmailId
                        });


                    }
                    LiveChatWidget.call("set_customer_name", vm.UserName);
                    LiveChatWidget.on('ready', onReady)
                }, 1000);
            }, 1000);

        },
        ModuleList: async function () {

            var vm = this;
            vm.isLoading = true;

            await DataService.GetModulesSubModules().then(function (response) {

                if (response.data != null) {

                    response.data.Data.forEach((value, index) => {

                        let myobj = {
                            ModuleId: value.ModuleId,
                            ModuleName: value.ModuleName,
                            icon: '<em class="fa ' + value.Icon + '" aria-hidden="true"></em>'

                        }
                        vm.ModulesList.push(myobj);
                        vm.isLoading = false;

                    })
                    vm.DefaultModuleId = vm.ModulesList[0].ModuleId;

                    vm.isLoading = false;
                }
                else {
                    vm.isLoading = false;
                }
            });
        },
        ArticleListPerModule: function (Id) {

            var vm = this;
            vm.ModuleId = Id;
            vm.isLoading = true;
            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=${Id}`

            DataService.GetArticleList(params).then(function (response) {

                if (response.data.length > 0) {
                    vm.HelpArticlePerModuleList = response.data;
                    vm.ModuleName = vm.HelpArticlePerModuleList[0].ModuleName;
                    vm.isLoading = false;
                    vm.IsDataShow = true;
                    vm.IsDataNotFoundShow = false;
                }
                if (response.data.length == 0) {
                    vm.HelpArticlePerModuleList = [];
                    vm.ModuleName = '';
                    vm.isLoading = false;
                    vm.IsDataNotFoundShow = true;
                    vm.IsDataShow = false;


                }
            });
            vm.SelectModuleId = Id;
        },

        DownloadUsermanual: function (Id) {

            var vm = this;
            var FileName = "Document.pdf";
            var params = `FileUrl=&ModuleId=${Id}`


            DataService.DownloadSingleFile(params).then((response) => {

                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;

                fileLink.setAttribute('download', FileName);
                document.body.appendChild(fileLink);
                fileLink.click();

            })
        },


        showSingle: function () {

            this.imgs = '/Content/images/vidoe-thumb.png'
            this.visible = true
            //indexRef = 1
        },

        onHide: function () {

            this.visible = false
        },
        SearchData: function (data) {
            debugger
            var vm = this;
            if (data != "") {
                vm.isLoading = true;
                var params = `search=${data}&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=`;

                DataService.GetArticleList(params).then(function (response) {

                    if (response.data.length > 0) {
                        vm.HelpArticlePerModuleList = response.data;
                        vm.isLoading = false;
                        vm.IsDataShow = true;
                        vm.IsDataNotFoundShow = false;
                    }
                    else if (response.data.length == 0) {
                        vm.isLoading = false;
                        vm.IsDataShow = false;
                        vm.IsDataNotFoundShow = true;
                    }
                    else {

                        vm.isLoading = false;
                        vm.IsDataShow = false;
                        vm.IsDataNotFoundShow = true;

                    }
                });

            }
            else {
                vm.ShowAlert(vm.$t("FillFealds"), "warning", true, vm.$t("Alert"));
            }

        },
        GetArticleContent: function (ArticlesId) {

            var vm = this;
            vm.ArticlesContent = '';
            var params = `ArticleId=${ArticlesId}&articleType=HelpArticle`;
            DataService.GetArticleDetailById(params).then(function (response) {

                if (response.data != null) {
                    vm.ArticlesContent = response.data.article_content;
                }
            });
        },



        // async enlargeImg() {

        //     this.mImage = document.getElementById("img1");

        //     this.mImage.style.transform = "scale(4.5)";

        //     this.mImage.style.transition = "transform 0.25s ease";



        // },

        // async resetImg() {

        //     this.mImage.style.transform = "scale(1)";
        //     this.mImage.style.transition = "transform 0.25s ease";
        // },


        // async ImageInModalPopup() {
        //     
        //     this.Modal1 = document.getElementById("myModal");

        //     this.Image1 = document.getElementById("myImg");

        //     this.Image2 = document.getElementById("img01");

        //     this.captionText = document.getElementById("caption");

        //     this.Modal1.style.display = "block";

        //     this.Image2.src = this.Image1.src

        //     this.captionText.innerHTML = this.Image1.alt;
        // },

        // async ClosePopup() {
        //    

        //     this.Modal1.style.display = "none";

        // },

