}
</script>

<style>
.ActiveList {
    max-height: 400px;
    overflow-y: auto;
    position: relative;

}

#myImg {
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    width: 50%;
    min-width: 50%;
    margin: 10px 0px;
}

#myImg:hover {
    background-color: #000;
    opacity: 0.5;
}
